import { useTranslation } from "react-i18next";
import ModalMenu from "../../../../Ui/Modal/ModalMenu";
import { useState } from "react";
import { useDepartmentRemoveHallTickect } from "../../../../Hooks/ApiHooks/SiteManager/site-manager-api";
const DepartmentSiteInternalExamCardMenu = ({
  did,
  onClose,
  menuData,
  onRefetch,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);

  const [departmentRemoveHallTickect] = useDepartmentRemoveHallTickect();

  const onDelete = () => {
    if (menuData?._id && did) {
      setDisabled((pre) => !pre);
      departmentRemoveHallTickect({
        did: did,
        bodyParameter: {
          pid: menuData?._id,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  return (
    <>
      <ModalMenu onClose={onClose} disabled={disabled}>
        <h6 onClick={onDelete}>{t("delete")}</h6>
      </ModalMenu>
    </>
  );
};

export default DepartmentSiteInternalExamCardMenu;
